/**
 * @file        
 * @CircleRef -   
 * @step -   
 * @stepsCount -   
 * @rotateOnStep -       
 * @returns void
 * @note    Circle      
 */
import { coordinatesGeneration } from "../helper";
import { gsap } from "gsap";
export function rotateOnStep(CircleRef, step, stepsCount) {
    if (CircleRef.current) {
        const points = [
            ...CircleRef.current.querySelectorAll(".point"),
            ...CircleRef.current.querySelectorAll(".activePoint"),
        ];
        gsap.to(CircleRef.current, {
            duration: 1,
            delay: 0.2,
            rotate: -(360 / stepsCount) * (step - 1),
            ease: "power2.out",
        });
        points.forEach((point) => {
            gsap.to(point, {
                duration: 0.37,
                rotate: (360 / stepsCount) * (step - 1),
                ease: "power2.out",
            });
        });
    }
}
export function startPosition(CircleRef, step, stepsCount) {
    if (CircleRef.current) {
        const points = [
            ...CircleRef.current.querySelectorAll(".point"),
            ...CircleRef.current.querySelectorAll(".activePoint"),
        ];
        const coordinates = coordinatesGeneration(stepsCount, step); //       
        points.forEach((point) => {
            const realIndex = Number(point.getAttribute("data-number")) - 1;
            gsap.set(point, {
                top: `${coordinates[realIndex].y}%`,
                left: `${coordinates[realIndex].x}%`,
            });
        });
    }
}
