/**
 *     
 * @yarsDatesGenerator -        
 * @first -   
 * @second -   
 */
import libDates from "../lib/dates";
export function yearsDatesGenerator(page) {
    const yearsArray = libDates[page - 1]
        .map((event) => {
        return { year: event[0], description: event[1] || "" };
    });
    return yearsArray;
}
/**
 *           0  99.
 *
 * @param num - ,   .
 * @returns        0  9,       1099,
 *           `null`,        (099).
 *
 * @example
 * formatNumber(5);   //  '05'
 * formatNumber(12);  //  '12'
 * formatNumber(-1);  //  null
 * formatNumber(100); //  null
 */
export function formatNumber(num) {
    if (num < 0 || num > 99) {
        return null;
    }
    if (num < 10) {
        return `0${num}`;
    }
    else {
        return num.toString();
    }
}
